






























































































































































































@use "~main/theme"

.pull-to-reveal
  position: fixed
  left: 0
  width: 100%
  z-index: 10
  top: -9999px

.showing-edge
  width: 100%
  height: 10px
  background-color: rgb(var(--color-gray-200))
  position: absolute
  top: 0
  left: 0
  z-index: -1

.spaceholder
  height: 0
