











































































































































































.navbar
  padding: 0.5rem 1rem

  a.navbar-brand
    margin-top: -3px

    ::v-deep img
      width: 232px

  .nav-item
    text-transform: uppercase

  .user-menu
    text-transform: none

  // a.glowing
  //   text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #9965f4, 0 0 70px #9965f4, 0 0 80px #9965f4, 0 0 100px #9965f4, 0 0 150px #9965f4

  .badge-btn
    position: relative
    height: 1.8rem
    margin-right: 1.5em

    img
      height: 1.3rem

    .badge
      position: absolute
      left: 22px
      top: 1px
      height: 18px
      border-radius: 4px
      // background-color: #9965f4
      transition: transform 0.2s

      /* Animation */
      &:hover
        transform: scale(1.3)

  @media (min-width: 992px)
    padding: 0rem 1rem

    .nav-item
      padding: 0.5rem 0.24rem
  
.detective-hours path
  fill: rgb(var(--color-dark-white))
