section:not(:first-child)
  margin-top: 60px

.theme-controls
  display: flex
  align-items: center

.theme-toggle
  display: inline-flex
  align-items: center
  background-color: rgb(var(--color-bg-secondary))
  border-radius: 100px
  position: relative
  margin-right: 26px

  &:hover
    cursor: pointer

  & > *
    position: relative
    z-index: 2

  .icon
    flex: 0 0 18px
    height: 18px
    margin: 10px

    path, circle
      fill: rgb(var(--color-icon-disabled))
      transition: all .3s ease-out

    &.active path, &.active circle
      fill: rgb(var(--color-icon-active))

  .label
    flex: 1
    text-align: center
    font-size: 12px
    color: #fff
    padding: 0 8px

  .active-indicator
    position: absolute
    width: calc(100% - 9px)
    height: 30px
    top: 0
    bottom: 0
    left: 0
    right: 0
    margin: auto
    z-index: 1
    transition: all .3s ease-out

    .circle
      position: absolute
      width: 30px
      height: 30px
      border-radius: 30px
      background-color: #fff
      transition: all .3s ease-in-out

    &.right
      transform: translateX(100%)

      .circle
        transform: translateX(-100%)

.system-theme-control
  position: relative
  z-index: 3

